/* Add this CSS to your stylesheet */
@keyframes loader {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loader {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 80%;
  height: 20px;
  animation: loader 0.8s linear infinite;
  margin: 0 auto;
}

